.presale {
  overflow: hidden;
  max-width: 100vw;
}
.presale > * {
  /* opacity: 0.1; */
}

.presale_bg {
  opacity: 1;
  position: absolute;
  top: -10%;
  left: 50%;
  transform: translateX(-50%);

  width: 100%;
  max-width: 1800px;
  max-height: 800px;

  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(0, 163, 255, 0.4) 0%,
    rgba(0, 26, 255, 0) 100%
  );
  background-repeat: no-repeat;

  z-index: -1;
}

@supports (aspect-ratio: 8/16) {
  .presale_bg {
    aspect-ratio: 12/8;
  }
}

@media screen and (max-width: 810px) {
  .presale_bg {
    width: 100%;
    top: -7%;
  }
}
@media screen and (max-width: 470px) {
  .presale_bg {
    width: 100%;
    top: -5%;
    background: radial-gradient(
      50% 50% at 50% 50%,
      rgba(0, 163, 255, 0.3) 0%,
      rgba(0, 26, 255, 0) 100%
    );
  }
}
