:root {
  --bg-color: #161623;
  --accent-color: #00a3ff;
  --light-accent-color: #38b6ff;
  --section-p-top: 100px;
  --debug-border: 2px dashed red;
}
::selection {
  background-color: var(--accent-color);
  color: black;
  text-shadow: none;
}
html {
  font-size: 16px;
}
body {
  overflow-x: hidden;
  min-height: 100vh;
  background-color: var(--bg-color);
}
.on_invisible_left {
  opacity: 0;
  transform: translateY(0) translateX(-80px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}
.on_invisible_right {
  opacity: 0;
  transform: translateY(0) translateX(80px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}
.on_invisible {
  opacity: 0;
  transform: translateY(80px) translateX(0);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}
.on_invisible_opacity {
  opacity: 0;
  transition: opacity 1200ms ease-in-out, transform 500ms ease-in-out;
}
.on_appearence {
  opacity: 1;
  transform: translateY(0) translateX(0);
}
.on_appearence_opacity {
  opacity: 1;
}
.App {
  overflow-x: hidden;
}
.container {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
}
.content {
  padding-inline: 120px;
  padding-block: 50px;
}
/* Text color */
.title {
  color: white;
}
.text {
  color: #5e779b;
}
/* Font Sizes */
.font10 {
  font-size: 10px;
}
.font12 {
  font-size: 12px;
}
.font14 {
  font-size: 14px;
}
.font16 {
  font-size: 16px;
}
.font18 {
  font-size: 18px;
}
.font24 {
  font-size: 24px;
}
.font36 {
  font-size: 36px;
}
.font_subtitle {
  font-size: 32px;
}
.font_title {
  font-size: 48px;
}
.blured {
  filter: blur(10px);
}
@media screen and (max-width: 1200px) {
  .container {
    max-width: 100vw;
    width: 100%;
    margin: 0 auto;
  }
  .content {
    padding-inline: 30px;
  }
}
@media screen and (max-width: 850px) {
  .content {
    padding-inline: 15px;
    padding-block: 25px;
  }
  .font_subtitle {
    font-size: 28px;
  }
  .font_title {
    font-size: 42px;
  }
  .font14 {
    font-size: 12px;
  }
  .font18 {
    font-size: 14px;
  }
  .font24 {
    font-size: 20px;
  }
  .font36 {
    font-size: 30px;
  }
}
@media screen and (max-width: 400px) {
  :root {
    --section-p-top: 50px;
  }
  .font_title {
  }
  .font_subtitle {
  }
}
