.games__hero {
  position: relative;

  width: 100%;

  margin-block: 30px;
  border-radius: 32px;
  background: radial-gradient(
    210.33% 907.91% at 98.58% 0%,
    #1b1b30 0%,
    #0b6cff 100%
  );
  overflow: hidden;
}
.games__hero_content {
  width: 100%;
  height: 100%;

  padding-block: 35px;
  padding-inline: 64px;
}
.games__hero_title {
}
.games__hero_text {
  /* width: 352px; */
}
.games__hero_info {
  position: relative;
  display: flex;
  align-items: center;

  width: 100%;

  margin-top: 30px;
  padding: 20px;
  border-radius: 12px;
  background: linear-gradient(
    90deg,
    rgba(63, 111, 253, 1) 0%,
    rgba(22, 22, 35, 0) 70%
  );
}
.games__hero_info_block {
  display: flex;
  width: 30%;
}
.info_block_img {
  width: 57px;
  height: 57px;
}
.info_block_stats {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 12px;
}
.info_block_stats_placeholder {
  height: 1rem;
  width: 5rem;
  border-radius: 100px;
  animation: loading_text 0.5s linear infinite alternate;
}
@keyframes loading_text {
  0% {
    background: rgba(46, 69, 109, 0.3);
  }
  100% {
    background: rgba(62, 92, 145, 0.3);
  }
}
.games__hero_bg_img {
  position: absolute;
  bottom: 0;
  right: 0;
}

@media screen and (max-width: 1270px) {
  .games__hero_info {
    height: 55%;
  }
  .games__hero_info_block {
    flex-direction: column;
    align-items: center;
  }
  .info_block_stats {
    margin-top: 10px;
    align-items: center;
  }
}
@media screen and (max-width: 800px) {
  .games__hero_content {
    padding-block: 25px;
    padding-inline: 25px;
  }
  .games__hero_info {
    align-items: flex-start;
    flex-direction: column;
  }
  .games__hero_info_block {
    margin-top: 20px;
    align-items: center;
    flex-direction: row;
    width: 100%;
  }
  .info_block_stats {
    width: 100%;
    align-items: flex-start;
  }
  .info_block_stats h3 {
    width: 100%;
  }
  .games__hero_bg_img {
    opacity: 0.5;
    bottom: -5%;
    width: 40%;
  }
}

@media screen and (max-width: 530px) {
  .games__hero_bg_img {
    opacity: 0.5;
    bottom: -10%;
    width: 45%;
  }
}
