@font-face {
  font-family: 'ArtegraSans-Bold';
  src: url('../fonts/ArtegraSans-Bold.eot');
  src: local('☺'), url('../fonts/ArtegraSans-Bold.woff') format('woff'),
    url('../fonts/ArtegraSans-Bold.ttf') format('truetype'),
    url('../fonts/ArtegraSans-Bold.svg') format('svg');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'ArtegraSans-SemiBold';
  src: url('../fonts/ArtegraSans-SemiBold.eot');
  src: local('☺'), url('../fonts/ArtegraSans-SemiBold.woff') format('woff'),
    url('../fonts/ArtegraSans-SemiBold.ttf') format('truetype'),
    url('../fonts/ArtegraSans-SemiBold.svg') format('svg');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'ArtegraSans-Medium';
  src: url('../fonts/ArtegraSans-Medium.eot');
  src: local('☺'), url('../fonts/ArtegraSans-Medium.woff') format('woff'),
    url('../fonts/ArtegraSans-Medium.ttf') format('truetype'),
    url('../fonts/ArtegraSans-Medium.svg') format('svg');
  font-weight: 500;
  font-style: normal;
}

.fw700 {
  font-family: 'ArtegraSans-Bold';
  font-weight: 700;
}
.fw600 {
  font-family: 'ArtegraSans-SemiBold';
  font-weight: 600;
}
.fw500 {
  font-family: 'ArtegraSans-Medium';
  font-weight: 500;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: 'ArtegraSans-SemiBold';
  font-size: 24px;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'ArtegraSans-Bold';
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a {
  /* text-decoration-skip-ink: auto; */
  text-decoration: none;
  margin: 0;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * {
  margin-top: 1em;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
