.hero {
  position: relative;

  height: 100vh;
  padding-top: 64px;
  padding-bottom: 0px;

  display: flex;
  flex-direction: column;
}
.hero::before {
  content: '';
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
  height: 100vh;
  background: radial-gradient(
    #00a3ff 10%,
    rgba(38, 5, 253, 0.5) 50%,
    var(--bg-color) 90%
  );
  filter: blur(50px);
  opacity: 0.2;

  z-index: -1;
}
.hero_content {
  position: relative;
  width: 100%;
  height: calc(100vh - 64px);
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}
.hero_main {
  display: flex;
  justify-content: center;
  height: 100%;
  flex: 1 1;
  flex-direction: column;
}
.hero_controls {
  display: flex;
  gap: 20px;
  margin-top: 70px;
}
.hero_button_link {
  width: 220px;
  height: 44px;
}
.hero_button {
  width: 100%;
  height: 100%;
}
.hero_mana {
  position: relative;
  display: flex;
  justify-content: center;
  flex: 1 1;
  flex-direction: column;
}
.mana_crystal,
.mana_crystal_glow {
  max-width: 577px;
  max-height: 577px;
  min-width: 480px;
  min-height: 480px;
  width: 100%;
  height: 100%;
  transform: rotateZ(10deg);
  animation: crystal-lifting 3s ease-in-out infinite alternate;
}
.mana_crystal_glow {
  position: absolute;
  top: 0;
  left: 0;
  animation: crystal-lifting 3s ease-in-out infinite alternate,
    crystal-glowing 30s ease-in-out infinite;
}
@keyframes crystal-lifting {
  0% {
    transform: translateY(0%) rotateZ(10deg);
  }
  100% {
    transform: translateY(-4%) rotateZ(10deg);
  }
}
@keyframes crystal-glowing {
  0% {
    filter: opacity(0);
  }
  5% {
    filter: opacity(0);
  }
  7% {
    filter: opacity(0.9);
  }
  12% {
    filter: opacity(0);
  }
  31% {
    filter: opacity(0);
  }
  33% {
    filter: opacity(0.9);
  }
  40% {
    filter: opacity(0);
  }
  45% {
    filter: opacity(0);
  }
  46% {
    filter: opacity(0.4);
  }
  49% {
    filter: opacity(0);
  }
  53% {
    filter: opacity(0);
  }
  54% {
    filter: opacity(0.9);
  }
  58% {
    filter: opacity(0);
  }
  66% {
    filter: opacity(0);
  }
  68% {
    filter: opacity(0.9);
  }
  75% {
    filter: opacity(0);
  }
  80% {
    filter: opacity(0);
  }
  81% {
    filter: opacity(0.3);
  }
  86% {
    filter: opacity(0);
  }
  88% {
    filter: opacity(0);
  }
  89% {
    filter: opacity(0.8);
  }
  99% {
    filter: opacity(0);
  }
  100% {
    filter: opacity(0);
  }
}

/* ** MEDIA ** */

@media screen and (max-width: 880px) {
  .hero_content {
    flex-flow: column-reverse nowrap;
    align-items: flex-end;
    justify-content: center;
  }
  .hero_main {
    width: 100%;
    justify-content: flex-start;
  }
  .hero_mana {
    flex: 0 1;
  }
  .mana_crystal,
  .mana_crystal_glow {
    min-width: 300px;
    min-height: 300px;
    width: 300px;
    height: 300px;
  }
}
@media screen and (max-width: 550px) {
  .hero_controls {
    justify-content: space-around;
  }
}
