.dropdown-apearence {
  opacity: 0;
  position: absolute;

  top: -1000px;
  left: 0px;

  width: 345px;
  height: 255px;

  border: none;
  background-color: var(--bg-color);

  transition: opacity ease-in-out 0.3s;
  z-index: 1;
}
.dropdown-apearence:hover {
  top: 41px;
  left: 0px;

  opacity: 1;
  z-index: 1;
}

.header__navbar-dropdown {
  padding: 32px 32px 12px;
  border-radius: 0px 0px 32px 32px;
  box-shadow: 0px 6px 32px rgba(10, 10, 10, 0.5),
    0px 0px 20px rgba(0, 0, 0, 0.08);
}
.dropdown__element {
  position: relative;
  display: flex;
  flex-flow: row nowrap;

  margin-bottom: 17px;

  cursor: pointer;
}
.dropdown-image {
  height: 41px;
  max-width: 41px;
  width: 100%;

  margin-right: 16px;
}
.dropdown__element div.dropdown-image {
  background-color: rgb(202, 202, 202);
  border-radius: 8px;
}
.dropdown-title {
  font-size: 16px;
}
.dropdown-text {
  font-size: 12px;
}
.dropdown__element::before {
  content: '';
  position: absolute;
  left: -15px;
  top: 0;
  opacity: 0;
  padding: 20px 1px;
  border-radius: 1.75em;
  background-color: var(--accent-color);
  transition: all ease-in-out 0.2s;
}
.dropdown__element:hover::before {
  opacity: 0.5;
}
