/*
==================
Burger Menu Component
==================
*/
.nav__navbar-burger {
  display: none;
  position: relative;
}
.burger__btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border: none;

  margin-left: auto;
  margin-right: 20px;

  border-radius: 10px;

  cursor: pointer;
  z-index: 999;
}
.burger__btn::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 80%;
  height: 80%;
  background-color: var(--accent-color);
  opacity: 0.7;
  filter: blur(15px);
}

@media screen and (max-width: 970px) {
  /* ======================= */
  /*       Burger Menu       */
  /* ======================= */
  .nav__navbar-burger {
    display: block;
  }
  .burger__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;

    width: 40px;
    height: 40px;

    background-color: transparent;

    cursor: pointer;
  }

  /* BURGER ICON */
  .burger__btn > span:first-child,
  .burger__btn > span:nth-child(2),
  .burger__btn > span:last-child {
    transition: ease-in-out 0.2s;
    display: block;
    position: absolute;
    width: 26px;
    height: 2px;
    background-color: white;
    border-radius: 100px;
    z-index: 999;
  }
  .burger__btn > span:first-child {
    display: block;
    top: 10px;
    left: 6.5px;
  }
  .burger__btn > span:nth-child(2) {
    display: block;
    top: 48%;
    left: 6.5px;
  }
  .burger__btn > span:last-child {
    display: block;
    top: 28px;
    left: 6.5px;
  }

  /* контейнер меню */
  .burger__box {
    transition: ease-in-out 0.3s;
    visibility: hidden;

    position: fixed;
    top: 50%;
    left: -100%;
    transform: translateY(-50%);

    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;

    width: 200px;
    height: 80vh;
    min-height: 200px;

    z-index: 0;

    border: 2px solid #00a3ff;
    border-left: none;
    border-top-right-radius: 32px;
    border-bottom-right-radius: 32px;
  }
  .burger__box::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
    min-height: 200px;

    border-top-right-radius: 32px;
    border-bottom-right-radius: 32px;
    background: linear-gradient(
      360deg,
      rgba(58, 162, 254, 0.2) 0%,
      rgba(100, 125, 253, 0.2) 0.01%,
      rgba(0, 163, 255, 0) 72.27%
    );
    background-color: var(--bg-color);
    z-index: -1;
    opacity: 1;

    transition: opacity 200ms ease-in-out;
  }
  .burger__box li {
    padding: 3% 0;
    width: 100%;
  }
  .burger__box li:first-child {
    padding-top: 30%;
  }
  /* элементы меню */
  .burger__item {
    color: white;
    display: block;
    text-decoration: none;
    width: 100%;
    padding: 5px 20px;
    cursor: pointer;
    font-size: 20px;
  }
  .burger__item:active {
    text-shadow: 0 0 35px rgba(0, 163, 255, 1);
  }
  .exit-btn {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: transparent;

    border: none;

    z-index: -1;
  }
}
/* UTILITY */
.active {
  transition: ease-in-out 0.3s;
  visibility: visible;
  left: 0;
}
.disabled {
  display: none;
  visibility: hidden;
}

/* CLOSE ANIMATION */
.crossed:first-child {
  opacity: 0;
}
.crossed:nth-child(2) {
  transform: rotate(-45deg);
}
.crossed:last-child {
  transform: rotate(45deg) translateX(-6px) translateY(-6px);
}

