/* ADDITIONAL MEDIA */
/* ADDITIONAL MEDIA */
.burger__box li:last-child {
  margin-top: auto;
  margin-bottom: 20%;
  display: flex;
}
.burger__media {
  position: relative;
  max-width: 30px;
  margin: 0px 20px;
}
.burger__media::after {
  content: '';
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);

  width: 30px;
  height: 30px;

  z-index: -1;
}
#telegram-link svg {
  transform: translateY(10%);
}
#twitter-link svg {
  transform: translateY(10%);
}
