.header {
  width: 100vw;
  height: 64px;
  position: fixed;

  z-index: 3;
  transition: background-color 500ms ease-in-out, top 1000ms ease-in-out,
    backdrop-filter 500ms ease-in-out;
}
.header-container {
  display: flex;
  align-items: center;

  width: 100%;
  max-width: 1920px;
  height: 100%;

  margin: 0 auto;

  z-index: 3;
}

.header_bg {
  backdrop-filter: blur(5px);
  background-color: rgba(22, 22, 35, 0.98);
}

@supports (backdrop-filter: blur(5px)) {
  .header_bg {
    backdrop-filter: blur(5px);
    background-color: rgba(22, 22, 35, 0.7);
  }
}
