.platform_game_card {
  position: relative;
  place-self: stretch;

  min-width: 320px;
  max-width: 380px;

  border-radius: 32px;

  overflow: hidden;
  transform: translateZ(0);
}

/* *** ON HOVER *** */
.platform_game_card:hover .platform_game_card_info {
  opacity: 1;
  transform: translateY(0px);
}
.platform_game_card:hover .platform_game_card_title {
  transform: translateY(50px);
}
.platform_game_card:hover .platform_game_card_devs {
  transform: translateY(50px);
}
.platform_game_card:hover .platform_game_card_title_ {
  transform: translateX(0);
  opacity: 1;
}
.platform_game_card:hover .game_card_bg_image::after {
  opacity: 0;
}
.platform_game_card:hover .game_card_bg_image::before {
  opacity: 1;
}
/* **************** */

@supports (aspect-ratio: 1 / 1.3) {
  .platform_game_card {
    aspect-ratio: 1 / 1.3;
  }
}
@supports not (aspect-ratio: 1 / 1.3) {
  .platform_game_card {
    height: 460px;
  }
}

.platform_game_card_bud {
  z-index: 2;
  position: absolute;
  top: 8px;
  left: 8px;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 25px 13px 13px 0px;

  height: 25px;
  padding-inline: 30px;

  background-color: var(--accent-color);
}

.platform_game_card_face {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  border-radius: 32px;
  overflow: hidden;
  transform: translateZ(0);
}
.platform_game_card_title {
  position: absolute;
  bottom: 15%;
  width: 100%;

  font-size: 46px;

  text-align: center;
  transition: transform 200ms ease-in-out;
}
.platform_game_card_title_ {
  position: absolute;
  top: 30%;
  left: 15px;
  font-size: 24px;
  transform: translateX(-50%);
  opacity: 0;
  transition: transform 200ms ease-in-out, opacity 200ms ease-in-out;
}
.platform_game_card_devs {
  position: absolute;
  bottom: 10%;
  width: 100%;

  text-align: center;
  transition: transform 200ms ease-in-out;
}

.game_card_bg_image {
  position: absolute;
  z-index: -2;
  top: 0;
  left: 0;
  max-width: 100%;
  width: 100%;
  transform: translateZ(0);
}

.game_card_bg_image::after,
.game_card_bg_image::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  border-radius: 32px;

  transition: opacity 200ms ease-in-out;
}
.game_card_bg_image::after {
  background: inherit;
  opacity: 1;
}
.game_card_bg_image::before {
  background: inherit;
  filter: brightness(0.5);
  opacity: 0;
}
.game_card_bg_image img {
  max-width: 100%;
  width: 100%;
  z-index: -2;
  border-radius: 32px;
  transition: opacity 200ms ease-in-out;
}

.platform_game_card_info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  display: flex;

  width: 100%;
  height: 55%;

  background: linear-gradient(180deg, #00a3ff 0%, #161623 100%);
  border-radius: 0px 0px 32px 32px;

  opacity: 0;
  transform: translateY(105%) scaleZ(0);
  transition: opacity 200ms ease-in-out, transform 300ms ease-in-out;
}
#game_card_info_about_label svg {
  position: absolute;
  transition: left 200ms ease-in-out;
}
#game_card_info_about_label,
#game_card_info_winners_label {
  display: flex;

  width: 144px;
  position: absolute;
  left: 0;
  cursor: pointer;
}
#game_card_info_about_label {
  top: -37.5px;
  left: 0px;
}
#game_card_info_winners_label {
  top: -18px;
  left: 122px;
}
#game_card_info_about_label span,
#game_card_info_winners_label span {
  position: absolute;
  top: 50%;
  left: 43%;
  transform: translateX(-50%) translateY(-50%);
}
#game_card_info_about_label span {
  top: 0%;
  transform: translateX(-50%) translateY(40%);
}

.card_info_about {
  position: absolute;
  top: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding: 15px;

  width: 100%;
  /* max-width: 352px; */

  height: 100%;

  transition: transform 200ms ease-in-out;
  /* transform: translateX(0); */
}
.card_info_about_apear {
  transform: translateX(0);
}
.card_info_about_disapear {
  transform: translateX(-100%);
}
.card_info_winners_apear {
  transform: translateX(0);
}
.card_info_winners_disapear {
  transform: translateX(100%);
}

/* *** */
.card_info_about > p {
  max-height: 70px;
  overflow-y: scroll;
  /* Firefox */
  scrollbar-width: none;
  /* IE and Edge */
  -ms-overflow-style: none;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.card_info_about > p::-webkit-scrollbar {
  display: none;
}
.card_info_ablout_button {
  height: 44px;
  width: 100%;
  background: #161623 !important;
  display: flex;
  align-items: center;
  margin-top: 24px;
  gap: 20px;
}
.card_info_ablout_button span svg {
  transform: translateY(25%) translateX(-20%);
}
.platform_game_card_stats {
  margin-top: 3%;
}
.platform_game_card_stats_row {
  display: flex;
}
.platform_game_card_stats_row p {
  line-height: 1;
  flex: 1 1 0%;
}
.platform_game_card_stats_row p span {
  font-size: 16px;
  transform: translateY(15%);
}
.platform_game_card_stats_row p span svg {
  transform: translateY(15%);
}

.card_info_winners {
  position: absolute;
  top: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  gap: 5px;

  width: 100%;
  height: 100%;
  padding: 10px;
  transition: transform 200ms ease-in-out;

  overflow-y: scroll;
  scrollbar-width: thin;
}

.card_info_winners_row {
  display: flex;
  align-items: center;

  width: 100%;
  min-height: 43px;

  padding-inline: 15px;

  border: 2px solid #00a3ff;
  box-shadow: 0px 6px 10px rgba(10, 10, 10, 0.25);
  border-radius: 21px;

  background: linear-gradient(
    90deg,
    rgba(22, 22, 35, 1) 50%,
    rgba(0, 163, 255, 0.1) 100%
  );
  filter: drop-shadow(0px 6px 32px rgba(10, 10, 10, 0.25));
}
.card_info_winners_row:first-child {
  filter: drop-shadow(0px 0px 0px rgba(10, 10, 10, 0));
}
.row_placeholder {
  background: rgba(5, 35, 56, 0.5);
  border: none;
  animation: loading 0.5s linear infinite alternate;
}
@keyframes loading {
  0% {
    background: rgba(6, 34, 55, 0.5);
  }
  100% {
    background: rgba(9, 59, 94, 0.5);
  }
}
.card_info_winners_row span {
}
.card_info_winners_row span:first-child {
  margin-right: 15px;
}
.card_info_winners_row span:last-child {
  margin-right: 0;
  margin-left: auto;
}

@media screen and (max-width: 1400px) {
  .platform_game_card_title {
    font-size: 32px;
  }
}
@media screen and (max-width: 800px) {
  .platform_game_card_stats_row p span {
    font-size: 14px;
  }
}
@media screen and (max-width: 730px) {
  .platform_game_card {
    justify-self: center;
    align-self: center;
  }
}
@media screen and (max-width: 600px) {
  .platform_game_card_stats_row p span {
    font-size: 12px;
  }
}
@media screen and (max-width: 420px) {
  .platform_game_card {
    min-width: 290px;
    max-width: 380px;
  }
}
